<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const init=()=>{
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closemuhuxinxiChartType1: true,
		hiddenmuhuxinxiChartType1: true,
		closemuhuxinxiChartType2: true,
		hiddenmuhuxinxiChartType2: true,
		closemuhuxinxiChartType3: true,
		hiddenmuhuxinxiChartType3: true,
		closemuhuxinxiChartType4: true,
		hiddenmuhuxinxiChartType4: true,
		closemuhuxinxiChartType5: true,
		hiddenmuhuxinxiChartType5: true,
	})
	const getCardList = () => {
		if(btnAuth('muhuxinxi','首页统计')){
			getmuhuxinxiChart1()
		}
		if(btnAuth('muhuxinxi','首页统计')){
			getmuhuxinxiChart2()
		}
		if(btnAuth('muhuxinxi','首页统计')){
			getmuhuxinxiChart3()
		}
		if(btnAuth('muhuxinxi','首页统计')){
			getmuhuxinxiChart4()
		}
		if(btnAuth('muhuxinxi','首页统计')){
			getmuhuxinxiChart5()
		}
	}
	const getmuhuxinxiChart1 = () => {
		nextTick(()=>{
			var shengchushuliangEchart1 = echarts.init(document.getElementById("muhuxinxishengchushuliangEchart1"),'macarons');
			context?.$http({
				url: `muhuxinxi/value/shengchuzhonglei/shengchushuliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].shengchuzhonglei);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].shengchuzhonglei
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '牲畜种类统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				shengchushuliangEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				shengchushuliangEchart1.setOption(option);
				//根据窗口的大小变动图表
				shengchushuliangEchart1.resize();
			})
		})
	}
	const getmuhuxinxiChart2 = () => {
		nextTick(()=>{
			var fanzhilvEchart2 = echarts.init(document.getElementById("muhuxinxifanzhilvEchart2"),'macarons');
			context?.$http({
				url: `muhuxinxi/value/shengchuzhonglei/fanzhilv`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].shengchuzhonglei);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].shengchuzhonglei
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '繁殖率统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				// 使用刚指定的配置项和数据显示图表。
				fanzhilvEchart2.clear()
				fanzhilvEchart2.setOption(option);
				//根据窗口的大小变动图表
				fanzhilvEchart2.resize();
			})
		})
	}
	const getmuhuxinxiChart3 = () => {
		nextTick(()=>{
        //  chushenglv
			var chushenglvEchart3 = echarts.init(document.getElementById("muhuxinxichushenglvEchart3"),'macarons');
