                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: ['25%', '55%'],
                            roseType: 'area',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				// 使用刚指定的配置项和数据显示图表。
				chushenglvEchart3.clear()
				chushenglvEchart3.setOption(option);
				//根据窗口的大小变动图表
				chushenglvEchart3.resize();
			})
		})
	}
	const getmuhuxinxiChart4 = () => {
		nextTick(()=>{
