				    },
				    tooltip: {
				      trigger: 'item',
				      formatter: '{b} : {c}'
				    },
				    xAxis: {
				        type: 'category',
				        boundaryGap: false,
				        data: xAxis
				    },
				    yAxis: {
				        type: 'value'
				    },
				    series: [{
				        data: yAxis,
				        type: 'line',
						areaStyle: {}
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				nongchanpinchanliangEchart4.clear()
				nongchanpinchanliangEchart4.setOption(option);
				//根据窗口的大小变动图表
				nongchanpinchanliangEchart4.resize();
			})
		})
	}
	const getmuhuxinxiChart5 = () => {
		nextTick(()=>{
        //  xiaoshouliang
			var xiaoshouliangEchart5 = echarts.init(document.getElementById("muhuxinxixiaoshouliangEchart5"),'macarons');
			context?.$http({
				url: `muhuxinxi/value/nongchanpinmingcheng/xiaoshouliang`,
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].nongchanpinmingcheng);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].nongchanpinmingcheng
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '农产品销量统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
