                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				// 使用刚指定的配置项和数据显示图表。
				xiaoshouliangEchart5.clear()
				xiaoshouliangEchart5.setOption(option);
				//根据窗口的大小变动图表
				xiaoshouliangEchart5.resize();
			})
		})
